.avatar{
    width: 40px;
    height: 40px;
    background-color: rgb(207, 207, 207);
    border-radius: 100px;
    margin-right: 18px;
    overflow: hidden;
    position: relative;

}
.avatar img {
    width: 120%;
    height: 120%;
    object-fit: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.item {
    padding: 10px 0;
    margin: 5px 0;
    color:rgb(78, 78, 78);
}